.team {
  background-color: #f7f7f7;
  position: relative;

  overflow: hidden;
}

.team > .bg-circles {
  &.pos-right {
    position: absolute;
    left: 100%;
    top: 0;
    transform: translate(-50%, -50%);
  }
}

//..................................

.sara_area {
  grid-area: sara_area;
}
.mohsen_area {
  grid-area: mohsen_area;
}
.ali_area {
  grid-area: ali_area;
}

.team {
  display: grid;
  justify-items: center;
  align-items: center;
  padding-left: 64px;
  padding-right: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
  box-sizing: border-box;

  .grid-container-team {
    position: relative;
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-areas: "sara_area ali_area mohsen_area";
    grid-column-gap: 10%;
    z-index: 2;

    width: 100%;
    height: calc((100vw - 64px - 24px) * 5 / 6);

    justify-items: stretch;
    align-items: stretch;
    justify-content: stretch;
    align-content: stretch;

    @media all, (max-height: calc((100vw - 64px - 24px) * 5 / 6 + 24px + 24px)) {
      height: 100%;
      width: calc((100vh - 24px - 24px) * 6 / 5);
    }
    @media all, (max-width: calc((100vh - 24px - 24px) * 6 / 5)) {
      max-width: calc((100vw - 64px - 24px) * 5 / 6 + 24px + 24px);
      max-height: 80vh;
    }

    /* #### Mobile Phones Portrait or Landscape #### */
    @media (max-device-width: 640px){
      // background-color: salmon;
      display: grid;
      grid-template-areas:
      "ali_area"
      "mohsen_area"
      "sara_area";
      grid-row-gap: 2%;
      // width: 80%;
      // height: 90%;
      // margin-top: 20%;
    }

    > .Rectangle {
      position: absolute;
      height: 100%;
      width: 100%;

      background-color: #ffffff;
      box-shadow: 7px 11px 16px -2px #e5e5e5;
      border-radius: 5vmin;

      text-align: center;
      // font-size: 30px;
      z-index: 2;

      // display: grid;
      // align-content: flex-start;
      // grid-template-rows: auto auto 1fr auto;

      /* #### Mobile Phones Portrait or Landscape #### */
      @media (max-device-width: 640px){
        // background-color: blue;
        // width: 100%;
        // height: 100%;
        // margin-top: 20%;
        width: 310px;
      }

      .text-team {
        font-family: "ourFont";
        text-align: center;
        // font-size: 25px;
        color: #454545;
        font-size: 2vmin;

        /* #### Mobile Phones Portrait or Landscape #### */
        @media (max-device-width: 640px){
          // font-size: 1vmin;
          position: absolute;
          top: 40%;
          right: 45%;

          // &.text1{}
          &.text2{padding-top: 20px;}
          &.text3{padding-top: 30px;}
          &.text4{padding-top: 40px;}
          &.text5{padding-top: 50px;}
        }

      }


      .text-team-name {
        font-family: "ourFont";
        text-align: center;
        color: #454545;
        // font-size: 35px;

        font-size: 3vmin;

        /* #### Mobile Phones Portrait or Landscape #### */
        @media screen and (max-device-width: 640px){
          position: absolute;
          top: 15%;
          right: 45%;
      
          // &.one{}
          &.two{padding-top: 20PX;}

        }

      }

      .images-team {
        width: 100%;
        height: auto;
        margin-top: 1vmin;
        margin-bottom: 1vmin;

        /* #### Mobile Phones Portrait or Landscape #### */
        @media (max-device-width: 640px){
          width: 100px;
          position: absolute;
          right: 5%;
          top: 10%;
        }

      }

      > .ovalButton {
        text-decoration: none;

        position: absolute;
        box-shadow: 2px 7px 8px -2px rgba(0, 0, 0, 0.05);
        border-radius: 4vmin;

        cursor: pointer;
        font-family: "ourFont";

        font-size: 2vmin;

        max-width: 200px;
        max-height: 60px;

        padding: 10px 20px 10px 20px;

        bottom: 5vh;

        // right: 10px;
        // left: 10px;

        right: 10%;
        left: 10%;

        &.Sara {
          background: #d9f1ff;
          color: #0b71aa;
        }
        &.Ali {
          background: #e7f2d9;
          color: #4e5545;
        }
        &.Mohsen {
          background: #f4e1ef;
          color: #88607d;
        }

        /* #### Mobile Phones Portrait or Landscape #### */
        @media (max-device-width: 640px){
          position: absolute;
          bottom: 10%;
        }

      }
    }
  }
}
